const hamburger = document.querySelector('.hamburger'),
      menu = document.querySelector('.navigation__list'),
      contactForm = document.querySelector('.contact-form'),
      buttonSubmitForm = document.querySelector('.form__btn'),
      buttons = document.querySelectorAll('.btn-submit'),
      checkbox = document.querySelector('.form__checkbox-input'),
      inputEmail = document.querySelector('.input-email'),
      EMAIL_PATTERN = /^(([^<>()[\].,;:\s@"]+(\.[^<>()[\].,;:\s@"]+)*)|(".+"))@(([^<>()[\].,;:\s@"]+\.)+[^<>()[\].,;:\s@"]{2,})$/iu;


hamburger.addEventListener('click', () => {
    menu.classList.toggle('navigation__list_active');
    hamburger.classList.toggle('hamburger__active');
});

buttons.forEach((elem) => {
    elem.addEventListener('click', () => {
        contactForm.classList.add('contact-form_active');
    })
});

buttonSubmitForm.addEventListener('click', () => {
    // event.preventDefault();

    if (!checkbox.checked) {
        checkbox.setCustomValidity('Упс!! Галку не поставили!');
    }

    // if (inputEmail.value !== EMAIL_PATTERN) {
    //     return false;
    // }

});
